import { FC, useState } from "react";

import { Box, Flex, Button } from "@chakra-ui/react";

import { FeaturedProjectCard, ImagePosition } from "pages/featured-projects/featured-project-card/FeaturedProjectCard";
import { configs } from "shared/content/Content";
import { ChevronDownIcon, ChevronUpIcon } from "utils/Icons";

const initialCount = 3;
const incrementor = 3;

export const FeaturedProjects: FC = () => {
    const [count, setCount] = useState<number>(initialCount);

    const scrollToElement = (idx: number) => {
        const {id} = configs.featuredProjects[idx];

        const elementTop = document
            .getElementById(`featured-project-card-${id}`)
            ?.getBoundingClientRect().top;

        if (elementTop) {
            window.scrollTo({ top: elementTop + window.scrollY - 64, behavior: "smooth" });
        }
    };

    const onShowMore = () => {
        const oldCount = count;
        setCount(count + incrementor);
        setTimeout(() => {
            scrollToElement(oldCount);
        }, 1);
    };

    const onShowLess = () => {
        setCount(initialCount);
        scrollToElement(initialCount);
    };

    return (
        <Box
            id="featured-projects"
            display={{ base: "block", md: "flex", lg: "block" }}
            flexDir="column"
            gap={{ base: "2", md: "1" }}
        >
            {configs.featuredProjects.map((project, idx) => (
                <div key={project.id}>{idx < count && 
                    <FeaturedProjectCard
                        imagePosition={idx % 2 === 0 ? ImagePosition.Right : ImagePosition.Left}
                        key={project.id}
                        {...project}
                    />}
                </div>
            ))}
            <Flex justifyContent="center" py="5" display={configs.featuredProjects.length > 3 ? "flex" : "none"}>
                {count < configs.featuredProjects.length ? (
                    <Button rightIcon={<ChevronDownIcon />} variant="link" style={{boxShadow: 'none'}} onClick={onShowMore}>
                        Show More
                    </Button>
                ) : (
                    <Button rightIcon={<ChevronUpIcon />} variant="link" style={{boxShadow: 'none'}} onClick={onShowLess}>
                        Show Less
                    </Button>
                )}
            </Flex>
        </Box>
    );
};
